@import "../../Common/commonStyle.less";

#homePage {
    p {
        margin: 0;
        padding: 0;
    }

    position: relative;
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    background-color: rgba(19, 20, 44, 1);

    header {
        position: relative;
        width: 100vw;
        height: 100*0.44vw;
        background:
            url('../../Assets/header/web_01@2x.png') 0 0 no-repeat,
            url('../../Assets/header/web_02@2x.png') 0 11vw no-repeat,
            url('../../Assets/header/web_03@2x.png') 0 22vw no-repeat,
            url('../../Assets/header/web_04@2x.png') 0 33vw no-repeat,
            url('../../Assets/header/web_05@2x.png') 0 44vw no-repeat;
        background-size: 100%;
        box-sizing: content-box;

        hgroup {
            position: absolute;
            width: 100%;
            top: 100*0.34vw;
            left: 0;
            right: 0;
            bottom: 0;
            margin: auto;
            color: #fff;
            text-align: center;
            z-index: 3;

            .banner-p1 {
                margin-bottom: 0;
                font-family: @PingFang-SC-Heavy-fontFamily;
                font-weight: 800;
                color: rgba(255, 255, 255, 1);
                animation: bannerP1 5s;
            }

            .banner-p2 {
                margin-bottom: 0;
                color: @fontColor;
                animation: bannerP1 5s;
            }
        }
    }

    // 头部与内容间线条样式
    .banner-line {
        position: absolute;
        width: 100%;
        height: 100*0.31vw;
        top: 32.5vw;
        background-image: url('../../Assets/header/line@2x.png');
        background-repeat: no-repeat;
        background-size: cover;
        animation: bannerLine 5s linear infinite;
        z-index: 1;
    }

    // 主体样式
    main {
        background-color: rgba(19, 20, 44, 1);

        // 解释文字
        .explanation-box {
            ul {
                position: relative;
                z-index: 1;
                display: flex;
                flex-direction: row;
                justify-content: space-around;

                li {
                    position: relative;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    background-image: url(../../Assets/home/explanation-border@2x.png);
                    background-size: 100% 100%;
                    background-repeat: no-repeat;

                    .explanation-logo {
                        position: relative;

                        .explanation-logo-img {
                            position: absolute;
                            width: 60%;
                            height: 60%;
                            margin-left: 20%;
                            margin-top: 20%;
                        }

                        .menu {
                            background-image: url(../../Assets/home/menu@2x.png);
                            background-size: 100% 100%;
                            background-repeat: no-repeat;
                        }

                        .aims {
                            background-image: url(../../Assets/home/aims@2x.png);
                            background-size: 100% 100%;
                            background-repeat: no-repeat;
                        }

                        .wallet {
                            background-image: url(../../Assets/home/wallet@2x.png);
                            background-size: 100% 100%;
                            background-repeat: no-repeat;
                        }
                    }

                    .line {
                        img {
                            width: 100%;
                        }
                    }

                    p {
                        display: inline-block;
                        font-family: @PingFangSC-Light-fontFamily;
                        color: rgba(255, 255, 255, 1);
                    }
                }

                li:hover,
                li:active,
                li:focus {
                    position: relative;
                    background-image: url(../../Assets/home/explanation-bg-hover@2x.png);
                    background-size: 100% 100%;
                    background-repeat: no-repeat;

                    .menu {
                        background-image: url(../../Assets/home/menu-hover@2x.png);
                        background-size: 100% 100%;
                        background-repeat: no-repeat;
                    }

                    .aims {
                        background-image: url(../../Assets/home/aims-hover@2x.png);
                        background-size: 100% 100%;
                        background-repeat: no-repeat;
                    }

                    .wallet {
                        background-image: url(../../Assets/home/wallet-hover@2x.png);
                        background-size: 100% 100%;
                        background-repeat: no-repeat;
                    }
                }
            }
        }

        // 特点
        .feature-box {
            &>section {
                display: flex;
                flex-direction: row;
                justify-content: flex-start;

                .feature-icon-box {
                    position: relative;
                    width: 42.7vw;

                    .feature-line {
                        position: absolute;
                    }

                    .feature-icon-lamp {
                        position: absolute;

                        img {
                            position: absolute;
                        }
                    }

                    .feature-base {
                        z-index: 1;
                    }
                }

                .feature-detail {
                    width: 40vw;

                    .feature-detail-title {
                        position: relative;
                        display: inline-block;
                        text-align: center;

                        img {
                            position: absolute;
                            left: 0;
                            right: 0;
                            margin: 0 auto;
                        }

                        span {
                            display: inline-block;
                            font-family: @PingFang-SC-Heavy-fontFamily;
                            font-weight: 800;
                            letter-spacing: 3px;
                            color: rgba(100, 223, 222, 1);
                        }
                    }

                    .feature-detail-txt {
                        display: block;

                        font-family: @PingFangSC-Light-fontFamily;
                        font-weight: 300;
                        color: rgba(255, 255, 255, 1);

                    }

                }
            }

            .feature-interesting {
                .feature-detail {
                    margin-left: 17.3vw;
                }
            }

            &>section:hover {
                .feature-detail-title {
                    span {
                        color: rgba(255, 255, 255, 1);
                        text-shadow: 0px 2px 14px rgba(100, 223, 222, 0.6);

                    }
                }

                .lightning {
                    animation: lightningLift 2s ease infinite alternate;
                }

                .smiley {
                    animation: smileyLift 2s ease infinite alternate;
                }

                .people {
                    animation: peopleLift 2s ease infinite alternate;
                }
            }
        }

        // 联系我们
        .contact-us-box {
            position: relative;

            .contact-us-bg {
                position: relative;
                height: 44vw;

                img {
                    position: absolute;
                }

                img:nth-child(1) {
                    width: 22.2vw;
                    top: 66%;
                }

                img:nth-child(2) {
                    width: 79vw;
                    left: 18.3vw;
                }

                img:nth-child(3) {
                    width: 7vw;
                    right: 27%;
                    animation: contactLogo 4s linear infinite alternate;

                }

                img:nth-child(4) {
                    right: 0;
                    top: 10%;
                    width: 15vw;
                }

                img:nth-child(5) {
                    right: 0;
                    top: 33%;
                    width: 6vw;
                }

                img:nth-child(6) {
                    width: 10vw;
                    right: 25.5%;
                    top: 2%;
                    animation: contactAdorn 5s linear infinite;

                }

                img:nth-child(7) {
                    width: 10vw;
                    left: 22.5%;
                    top: 35%;
                    opacity: .6;
                    animation: contactAdorn2 5s linear infinite;
                }

                img:nth-child(8) {
                    width: 10vw;
                    left: 22.5%;
                    top: 14%;
                    opacity: .6;
                    animation: contactAdorn3 5s linear infinite;

                }
            }

            .contact-us-txt {
                position: absolute;

                p {
                    font-family: @PingFangSC-Regular-fontFamily;
                    font-weight: 400;
                    color: rgba(255, 255, 255, 1);
                }

                .uploadBtn {
                    position: relative;
                    display: flex;
                    flex-direction: row;
                    justify-content: flex-start;
                    align-items: center;
                    background: none;
                    border: none;
                    outline: none;
                    cursor: pointer;
                    padding: 0;
                    border-radius: 40px;

                    box-shadow: 0px 2px 10px 0px rgba(100, 223, 222, 0.1);

                    .btn-bg {
                        width: 100%;
                    }

                    span {
                        font-family: @PingFangSC-Regular-fontFamily;
                        font-weight: 400;
                        color: rgba(255, 255, 255, 1);
                    }

                    .btn-icon {
                        position: absolute;
                        width: 4.5vw;
                    }

                    .upload-txt {
                        position: absolute;
                        left: 32%;

                    }

                    .googlePlay {
                        margin-right: 7px;
                    }
                }


            }
        }
    }
}

.homePage-pc {
    .record {
        padding-bottom: 10px;
    }

}




