@keyframes bannerP1 {
    0% {
        transform: scale(2);
        opacity: 0;
    }

    100% {
        transform: scale(1);
        opacity: 1;
    }
}

@keyframes bannerLine {
    0% {
        top: 40.5vw;
        opacity: 0;
    }

    50% {
        top: 35.5vw;
        opacity: 1;
    }

    100% {
        top: 30.5vw;
        opacity: 0;
    }
}

@keyframes adornLift {
    0% {
        top: -6%;
        opacity: 0;
    }

    100% {
        top: -18%;
        opacity: 1;
    }
}

@keyframes lightningLift {
    0% {
        top: -5%;
        opacity: 0;
    }

    100% {
        top: -13%;
        opacity: 1;
    }
}

@keyframes smileyLift {
    0% {
        top: -14%;
        opacity: 0;
    }

    100% {
        top: -20%;
        opacity: 1;
    }
}

@keyframes peopleLift {
    0% {
        top: -12%;
        opacity: 0;
    }

    100% {
        top: -20%;
        opacity: 1;
    }
}

@keyframes contactLogo {
    0% {
        top: 0%;
        opacity: 0;
    }

    50% {
        top: -3%;
        opacity: 1;
    }

    100% {
        top: -6%;
        opacity: 0;
    }
}

@keyframes contactAdorn {
    0% {
        top: 2%;
        opacity: 0;
    }

    50% {
        top: -2%;
        opacity: 1;
    }

    100% {
        top: -6%;
        opacity: 0;
    }
}

@keyframes contactAdorn2 {
    0% {
        top: 35%;
        opacity: 0;
    }

    50% {
        top: 31%;
        opacity: .6;
    }

    100% {
        top: 27%;
        opacity: 0;
    }
}

@keyframes contactAdorn3 {
    0% {
        top: 14%;
        opacity: 0;
    }

    50% {
        top: 10%;
        opacity: .6;
    }

    100% {
        top: 6%;
        opacity: 0;
    }
}